.NavigationItem{
    margin:10px 0; 
    -webkit-box-sizing: border-box; 
            box-sizing: border-box; 
    display: block; 
    width: 100%;
}

.NavigationItem a{
    color: #1670d8; 
    text-decoration: none; 
    width: 100%; 
    -webkit-box-sizing: border-box; 
            box-sizing: border-box; 
    display: block; 
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{ 
    color: #40A4C8; 
}

@media(min-width:500px) {
    .NavigationItem{
        margin:0; 
        display: -ms-flexbox; 
        display: flex; 
        height: 100%;
        width: auto; 
        -ms-flex-align: center; 
            align-items: center; 
    }
    
    .NavigationItem a{
        color:black; 
        font-weight: bold; 
        text-decoration: none; 
        height: 100%; 
        padding: 16px 10px; 
        border-bottom: 4px solid transparent;
      
    }
    
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active{ 
        background-color: #1670d8;
        border-bottom: 4px solid #40A4C8;
        color: black;
        font-weight: bold;  
    }

}


